import React from "react";

import {
  DataTable,
  DataTableColumn,
  ScopedNotification,
  Button,
  ButtonGroup
} from "@salesforce/design-system-react";
import { moment } from "../lib/utils";
import { $, baseUrl } from "../lib/grax-data-lake";

const TS_FORMAT = "l LT";
const TIME_INTERNAL_DAYS = 7;
const PAGE_SIZE = 25;

const columns = [
  <DataTableColumn key="num-records" label="# of Records" property="total" />,
  <DataTableColumn key="obj-type" label="Object Type" property="objectType" />,
  <DataTableColumn key="queued" label="Queued" property="statusQueued" />,
  <DataTableColumn key="done" label="Done" property="statusDone" />,
  <DataTableColumn key="completed" label="% Completed" property="completed" />,
  <DataTableColumn key="start-time" label="Start Time" property="startDate" />,
  <DataTableColumn key="start-time" label="End Time" property="endDate" />
];

export default class GRAXRestoreStatusTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pageNum: 0,
      items: []
    };
    this.allItems = [];
  }

  componentDidMount() {
    this.loadData();
  }

  onDataLoaded = data => {
    if (Array.isArray(data)) {
      // pre-process raw data coming in
      data.forEach(record => {
        // eslint-disable-next-line no-param-reassign
        record.completed = `${Math.round((record.statusDone * 100) / record.total)}%`;
        // eslint-disable-next-line no-param-reassign
        record.startDate = record.startDate ? moment(record.startDate).format(TS_FORMAT) : "";
        // eslint-disable-next-line no-param-reassign
        record.endDate = record.endDate ? moment(record.endDate).format(TS_FORMAT) : "";
      });
      this.allItems = data;
      this.updatePage(0);
    } else {
      this.allItems = [];
    }
  };

  onDataLoadError = (xhr, status, err) => {
    this.setState({
      error: err.toString()
    });
  };

  loadData = () => {
    this.setState({ pageNum: 0, items: [] });

    $.ajax({
      type: "GET",
      url: `${baseUrl}/grax-app/grax-data-lake/asyncQueue/status`,
      data: {
        days: TIME_INTERNAL_DAYS
      },
      success: this.onDataLoaded,
      error: this.onDataLoadError
    });
  };

  updatePage = pageNum => {
    this.setState({
      pageNum,
      items: this.allItems.slice(pageNum * PAGE_SIZE, (pageNum + 1) * PAGE_SIZE)
    });
  };

  nextPage = () => {
    const { pageNum } = this.state;
    if ((pageNum + 1) * PAGE_SIZE <= this.allItems.length) {
      this.updatePage(pageNum + 1);
    }
  };

  prevPage = () => {
    const { pageNum } = this.state;
    if (pageNum > 0) {
      this.updatePage(pageNum - 1);
    }
  };

  render() {
    const { items, error, pageNum } = this.state;
    return (
      <>
        <div className="-x-small-buttons--horizontal slds-p-bottom_large">
          <Button label="Retrieve" variant="brand" onClick={this.loadData} />
        </div>
        {error && (
          <div style={{ clear: "both" }}>
            <ScopedNotification theme="dark">
              <p>{error}</p>
            </ScopedNotification>
          </div>
        )}
        {items.length > 0 && (
          <>
            <DataTable style={{ marginBottom: "5px" }} items={items}>
              {columns}
            </DataTable>
            {PAGE_SIZE < this.allItems.length && (
              <ButtonGroup>
                <Button
                  iconCategory="utility"
                  iconName="chevronright"
                  iconPosition="right"
                  variant="neutral"
                  disabled={pageNum === 0}
                  label={`previous ${PAGE_SIZE}`}
                  onClick={this.prevPage}
                />
                <Button
                  iconCategory="utility"
                  iconName="left"
                  iconPosition="left"
                  variant="neutral"
                  disabled={(pageNum + 1) * PAGE_SIZE >= this.allItems.length}
                  label={`next ${PAGE_SIZE}`}
                  onClick={this.nextPage}
                />
              </ButtonGroup>
            )}
          </>
        )}
      </>
    );
  }
}
